import React from 'react';
import PropTypes from 'prop-types';
import withStyles from 'material-ui/styles/withStyles';
import Typography from 'material-ui/Typography/Typography';
import Avatar from 'material-ui/Avatar/Avatar';

import logoBlue from '../../assets/logo/blue.svg';
import logoWhite from '../../assets/logo/white.svg';

const styles = theme => ({
  root: {
    padding: theme.spacing.unit * 2,
    verticalAlign: 'top'
  },
  images: {
    display: 'flex',
    marginBottom: theme.spacing.unit * 2,
    alignItems: 'center',
    justifyContent: 'space-between'
  },
  image: {
    width: 64,
    height: 64
  }
});

const UserData = ({ photoURL, displayName, email, classes, theme }) => (
  <div className={classes.root}>
    <div className={classes.images}>
      <Avatar src={photoURL} alt="avatar" className={classes.image} />
      <img
        src={theme.palette.type === 'dark' ? logoWhite : logoBlue}
        alt="logo"
        className={classes.image}
      />
    </div>

    <Typography variant="body2">{displayName}</Typography>
    <Typography variant="body1">{email}</Typography>
  </div>
);

UserData.propTypes = {
  photoURL: PropTypes.string,
  displayName: PropTypes.string,
  email: PropTypes.string,
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired
};

export default withStyles(styles, { withTheme: true })(UserData);
